<template>
  <div class="listcomment" >
    <table class="table">
     <thead>
      <tr>
        <th scope="col">댓글 내용</th>
        <th scope="col">공개 여부</th>
      </tr>
     </thead>
     <tbody>
      <tr v-for="(list, i) in state.lists" :key="(list, i)">
        <td>
          <RouterLink :to="{name: 'answerread', params:{id:list.answerid}}"
            style="color:rgba(6, 90, 92, 0.9); text-decoration:none; font-weight:bold;" >
            {{ list.content }}
          </RouterLink>
        </td>
        <td> <button class="btn secondary btn-sm" @click="sendId(list.commentid),deleteModal()">x</button> </td>
      </tr>
     </tbody>
   </table>
</div>
</template>


<script>
import { onMounted, reactive, ref } from 'vue'
import axios from 'axios'
import { useRouter } from 'vue-router';


export default {
  name: "TheQuestion",



  setup() {
    onMounted(() => {
      loginHandler();
    })


    const value = ref(false)
    const router = useRouter()


 const state = reactive({
     questionid :'',
     cid:'',
     lists: '',
     email: sessionStorage.getItem('email'),
     token: sessionStorage.getItem('TOKEN'),
   });

  const sendId = ( commentid) =>{
    modal.cid = commentid
  };

   const modal = reactive({
    cid:'',
   })

   const loginHandler = async () => {
    //  console.log("PrivateComment by Email launch")
   const url= "http://43.201.60.125/api/commentlistbyemail"

   const headers = {
     //TODO send the right amound of tokent and receive the same...
     'Content-Type' : 'application/json',
     Authorization: state.token,
     token: state.token,
     'name': 'test list'
   }
     const body = { email: state.email }
     await axios.post(url, body, { headers }).then(function (res) {
       if(res.status==200){
         state.lists = res.data
       } else {
         alert('bad.')
       }
     })
   }

   const deleteModal = () => {
    ElMessageBox.confirm(
      'Do you really want to delete your comment?',
      'Warning',
      {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning',
      }
    )
      .then(() => {
        ElMessage({
          type: 'success',
          message: 'Delete completed',
        })
        deleteComment()
      })
      .catch(() => {
        ElMessage({
          type: 'info',
          message: 'Delete canceled',
        })
      })
    }

    const deleteComment = async()=>{
    const url ='http://43.201.60.125/api/deleteComment'
    const headers = {
        "Content-Type" : "application/json",
        Authorization: state.token,
        "token": state.token }
        const body ={commentid : modal.cid,}
        console.log("delete comment... launch  " + modal.cid)
    await axios.post(url, body, {headers}).then(function(res){
      console.log("delete comment after axios" )
      if(res.status === 200){
        console.log(`comment${modal.cid} is deleted`);
        setTimeout(loginHandler(),1000);
        }
      })
    }

   return{  value, state, loginHandler,  deleteModal, deleteComment, sendId, modal }

 }

};


</script>

<style scoped>
.answer_collapse {
  border: solid 1px grey;
  padding: 0 10px;
  margin: 10px 0;
}

.answer {
  display: flex;
}


.counter{
   display:  flex;
   align-items: center;
   height: 80px;
   margin-right: 30px;
   background-color:lightgray;
   padding: 0 10px;
   border-radius: 30%;
 }

 .num{
   font-size: 30px;
   color: rgb(6, 90, 92);
   font-weight: bold;
   margin: 10px;
 }



.num {
  font-size: 30px;
  color: rgb(6, 90, 92);
  font-weight: bold;
  margin: 10px;
}

button {
  font-size: 20px;
  font-weight: bold;
}
</style>